"use c"
import React from "react";
import Input from "../components/ui/Input";
import BackArrow from "../components/ui/BackArrow";

const page = () => {
  return (
    <div>
      <div className="flex flex-col gap-8">
        <BackArrow />
        <div className="mt-8">
          <Input
            isLocation
            headline="كافهينو"
            placeholder="المعادي - شارع النصر"
          />
        </div>
      </div>
    </div>
  );
};

export default page;
